@import url(https://fonts.googleapis.com/css2?family=Varela+Round&display=swap);
html,body{height:100%}body{margin:0;font-family:'Valera Round', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background:var(--global-background-color)}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}#root{z-index:-101;display:grid;grid-template-rows:var(--global-y) calc(100vh - var(--global-y));font-size:var(--global-font-size);letter-spacing:-1px;font-weight:700}h1,h2,h3,h4,h5,h6{padding:0;margin:0}


.section{background:var(--global-color__white);border:var(--global-border-thickness) solid var(--global-color__black);border-radius:var(--global-border-radius);padding:var(--global-padding)}.section+.section{margin-top:var(--global-margin)}

.dropdown{background:var(--global-color__white);border-radius:var(--global-border-radius);border:var(--global-border-thickness) solid var(--global-color__black);padding:var(--global-padding);font-size:var(--global-font-size-sm);font-weight:700}


.masthead{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-between;justify-content:space-between;padding:0 var(--global-padding);border-bottom:var(--global-border-thickness) solid var(--global-color__black)}


.button{background:var(--global-color__white);border-radius:var(--global-border-radius);border:var(--global-border-thickness) solid var(--global-color__black);padding:var(--global-padding);font-size:var(--global-font-size-sm);font-weight:700;cursor:pointer}

.page-landing{display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;-webkit-flex-direction:column;flex-direction:column}.page-landing .landing-form{margin-top:calc(var(--global-y) * -1);display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.page-landing .landing-form *+*{margin-top:var(--global-margin)}

.page-find{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;margin:var(--global-y)}.page-find .section-results{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;text-align:center;-webkit-align-items:center;align-items:center;padding:var(--global-padding-lg)}.page-find .section-results-title{font-weight:300}.page-find .section-results *+*{margin-top:var(--global-margin)}

.currency{display:-webkit-inline-flex;display:inline-flex;padding:8px;border-radius:var(--global-border-radius)}.currency sub{margin-left:4px;font-size:12px;margin-top:auto;margin-bottom:4px}.currency-USD{background:var(--global-color__green-200);color:var(--global-color__green-100);border:1px solid var(--global-color__green-100)}.currency-ETH{background:var(--global-color__red-200);color:var(--global-color__red-100);border:1px solid var(--global-color__red-100)}.currency-LTC{background:var(--global-color__blue-200);color:var(--global-color__blue-100);border:1px solid var(--global-color__blue-100)}.currency-BTC{background:var(--global-color__#ff0-200);color:var(--global-color__#ff0-100);border:1px solid var(--global-color__#ff0-100)}


:root{--global-color__black: #242423;--global-color__white: #fafafa;--global-color__green-100: #a5d651;--global-color__green-200: #f1fcd3;--global-color__red-100: #f95657;--global-color__red-200: #ffdfe3;--global-color__blue-100: #4dbcfb;--global-color__blue-200: #e0f4ff;--global-color__yellow-100: #ecb417;--global-color__yellow-200: #fff8dd;--global-font-size: 1.5rem;--global-font-size-sm: 1rem;--global-font-size-md: var(--global-font-size);--global-font-size-lg: 2rem;--global-background-color: #fdf1db;--global-border-thickness: 3px;--global-padding: 12px;--global-padding-sm: 8px;--global-padding-md: var(--global-padding);--global-padding-lg: 16px;--global-padding-xl: 24px;--global-margin: 16px;--global-margin-sm: 12px;--global-margin-md: var(--global-margin);--global-margin-lg: 24px;--global-margin-xl: 32px;--global-y: 100px;--global-border-radius: 5px}

